import React, { useEffect, useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Items from '../components/ListItems/Items';
import DenseAppBar from '../components/HeaderBar/HeaderBar';
import Fade from '@material-ui/core/Fade';
import InstallBanner from '../components/InstallBanner';
import { HideOnScroll } from '../components/BottomNavigation/HideOnScrool';
import { fakeAPIList } from '../services/fakeAPIData';
import { CircularProgress } from '@material-ui/core';

const useStyles = makeStyles(theme => ({
    root: {
        width: '100vw',
        backgroundColor: theme.palette.background.paper,
    },
}));




export default function DanhSachSuKien(props) {

    const classes = useStyles();
    const [data, setData] = useState([]);


    useEffect(() => {
        setTimeout(() => {
            setData([...fakeAPIList]);
        }, 300);
    }, []);


    console.log("data", data);




    return (
        <>
            <div className={classes.root}>
                <DenseAppBar
                    title={"Danh Sách Sự Kiện"}
                />

                {
                    data.length > 0 ?
                        <>
                            {
                                data.map((item, index) => {
                                    return (
                                        <Items               
                                            key={index}
                                            item={item}
                                        />
                                    )
                                })
                            }
                        </>
                        :
                        <CircularProgress color="secondary" />
                }


                <InstallBanner />

            </div>
        </>
    )
}