import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import { ListItem, ListItemText, Grid } from '@material-ui/core';
import {
    Link
} from "react-router-dom";


const useStyles = makeStyles(theme => ({
    root: {
        // padding: theme.spacing(3, 2),
        // maxWidth: 360,

        backgroundColor: theme.palette.background.paper,
    },
    block: {
        display: 'block',
        color: "black"
    },
}));

export default function Items(props) {
    const { status, item } = props;
    const classes = useStyles();

    return (
        <Paper className={classes.root}>
            <ListItem
                component={Link}
                to={{
                    pathname: `/detail/${item.id}`,
                    item
                }}
                alignItems="center">
                <ListItemText
                    primary={item.title}
                    secondary={
                        <React.Fragment>
                            {item.subtitle}
                        </React.Fragment>
                    }
                />


                {
                    status ?
                        <Grid
                            container
                            alignItems="flex-end"
                            direction="column"
                        >
                            <Grid
                                item
                            >
                                <Typography
                                    component="span"
                                    variant="body2"
                                    color="textSecondary"
                                >
                                    {"08:30"}

                                </Typography>
                            </Grid>
                            <Grid
                                item
                            >
                                <Typography
                                    component="span"
                                    variant="body2"
                                    // className={classes.inline}
                                    color="textSecondary"
                                >
                                    {item.dataExpire}

                                </Typography>
                            </Grid>


                        </Grid>
                        :
                        <Grid
                            container
                            alignItems="flex-end"
                            direction="column"
                        >
                            <Typography
                                component="span"
                                variant="body2"
                                // className={classes.inline}
                                color="textSecondary"
                            >
                                {"Đã kết thúc"}
                            </Typography>
                        </Grid>
                }

            </ListItem>
        </Paper>
    );
}



Items.defaultProps = {
    status: false,
    item: {
        id: 2,
        title: "PAKDD-2019",
        subtitle: "The 23rd Pacific-Asia Conference on Knowledge Discovery and Data Mining",
        dataExpire: "14/4/2019",
        status: false,
        address: "Thái Bình.VAST, IOIT, Trường ĐH Thái Bình"
    }
}